.App {
  text-align: center;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  display: grid;
  grid:
    [viewport-top] 10px
    [toast-top] auto
    [toast-bottom] 1fr
    [toolbar-top] 40px
    [toolbar-bot] 5px
    [viewport-bot]/
    [viewport-left] minmax(0, 1fr) [toolbar-left]auto[toolbar-right]minmax(0, 1fr)[viewport-right];
}

.App > .grid {
  grid-area: viewport-top / viewport-left / viewport-bot / viewport-right;
  position: relative;
  z-index: 1;
}

.App > .toolbar {
  grid-area: toolbar-top / toolbar-left / toolbar-bot / toolbar-right;
  z-index: 10000;
}

.App > .toastArea {
  grid-area: toast-top / viewport-left / toast-bottom / viewport-right;
  place-self: center center;
  max-width: 700px;
  width: 100%;
  z-index: 1000000;
}

:root {
  --accent: #4242ef;
  --background: #ffffff;
  --text: #020210;
}

/* switch values over dark color scheme */
@media (prefers-color-scheme: dark) {
  :root {
    --background: #020210;
    --text: #ffffff;
  }
}